.container-text-image {
  position: relative;
  background: #006635;
  z-index: 0;
}

.show-before::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 85%;
  background: url("../../assets/images/before_sobre_sec1.png") no-repeat left bottom;
  background-size: contain;
  opacity: 0.2;
  z-index: -1;
}

.show-after::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background: url("../../assets/images/before-section.png") no-repeat left bottom;
  background-size: cover;
  opacity: 0.1;
  filter: invert(1);
  -webkit-mask-image: linear-gradient(to top, rgb(0, 0, 0), rgba(0, 0, 0, 0));
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: cover;
  mask-image: linear-gradient(to top, rgb(0, 0, 0), rgba(0, 0, 0, 0));
  mask-repeat: no-repeat;
  mask-size: cover;
  z-index: -2;
}
.show-after::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 70%;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.4), transparent);
  z-index: -1;
}
.show-after.after-top::before, .show-after.after-top::after {
  top: 0;
  bottom: inherit;
}
.show-after.after-top::before {
  transform: rotate(180deg);
}
.show-after.after-top::after {
  background: linear-gradient(to top, transparent, rgba(0, 0, 0, 0.4));
}

